
import React, { useEffect, useState } from 'react';
import {

    Button,

} from "react-bootstrap";
import axios from "axios"
import { confirmAlert } from "react-confirm-alert";
import "react-confirm-alert/src/react-confirm-alert.css"; 

var urlbackend = require("../../../env/env")();
function Requirepayment(props) {
    //const [pickupsUpdate] = useState(props.pickup)
    const handleShow = () => {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        confirmAlert({
            title: "Confirmer Demande?",
            message: "",
            buttons: [
              {
                label: "Yes",
                onClick: () => {
                  axios
                    .post(urlbackend+"/requirepayment/add",{},config)
                    .then((res) => {
                      console.log(res);
                      alert("votre demande sera traitée la plus tôt possible")
                     
                    }).catch((e)=>{
                        console.log(e)
                    })
                },
              },
              {
                label: "No",
                onClick: null,
              },
            ],
          });

    };
    useEffect(() => {
    }, [])

    return (
        <>
            <Button variant="secondary"
                onClick={handleShow}> <span className="fa fa-ticket"></span> Demande de payment</Button>

        </>
    );
}

export default Requirepayment